import useSkillQuery from "@/hooks/useSkillQuery";
import { getClients } from "@/lib/api/clientApi";
import { Client } from "@/types/client";
import { Job, JobFilters, JobSort, PagedJobs } from "@/types/job";
import { Filter, Search } from "lucide-react";
import { useEffect, useState } from "react";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "../ui/card";
import CustomCombobox from "../ui/CustomCombobox";
import { Input } from "../ui/input";
import JobTable from "./job-table";

interface JobTableContainerProps {
  pagedJobs?: PagedJobs;
  filters: JobFilters;
  setFilters: React.Dispatch<React.SetStateAction<JobFilters>>;
  handleViewDetails: (job: Job) => void;
  handleEditJob: (job: Job) => void;
  handleDeleteJob: (jobId: string) => void;
}

const JobTableContainer = ({
  pagedJobs,
  filters,
  setFilters,
  handleViewDetails,
  handleEditJob,
  handleDeleteJob,
}: JobTableContainerProps) => {
  // #region API
  const { data: clients } = useSkillQuery({
    queryKey: ["clients"],
    queryFn: () => getClients(),
  });

  // #region State
  const [sort, setSort] = useState<JobSort>({
    field: "incomingDate",
    direction: "desc",
  });

  const [filteredJobs, setFilteredJobs] = useState<Job[]>(
    pagedJobs?.items || []
  );

  // #region hooks
  // Apply filters whenever they change
  useEffect(() => {
    applyFiltersAndSort();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [clients, filters, sort]);

  // Get unique values for filter options
  // const jobTypes = Array.from(
  //   new Set(jobs.map((j) => j.jobType).filter(Boolean))
  // );
  // const vendors = Array.from(
  //   new Set(jobs.map((j) => j.vendor).filter(Boolean))
  // );

  const applyFiltersAndSort = () => {
    let updatedJobs = pagedJobs?.items || [];

    // Apply search filter
    if (filters.search) {
      const searchLower = filters.search.toLowerCase();
      updatedJobs = updatedJobs.filter((job) =>
        job.jobCode.toLowerCase().includes(searchLower)
      );
    }

    // Apply client filter
    if (filters.clientId && filters.clientId !== "") {
      updatedJobs = updatedJobs.filter(
        (job) => job.clientId === filters.clientId
      );
    }

    // Apply sorting
    // updatedJobs = updatedJobs.sort((a, b) => {
    //   const fieldA = a[sort.field];
    //   const fieldB = b[sort.field];
    //   if (fieldA < fieldB) return sort.direction === "asc" ? -1 : 1;
    //   if (fieldA > fieldB) return sort.direction === "asc" ? 1 : -1;
    //   return 0;
    // });
    setFilteredJobs(updatedJobs);
  };

  const handleFilterChange = (key: keyof JobFilters, value: string) => {
    setFilters((prev: JobFilters) => ({ ...prev, [key]: value }));
  };

  const setCurrentPage = (page: number) => {
    setFilters((prev) => ({ ...prev, pageNumber: page }));
  };

  // Handle sort changes
  const handleSort = (field: keyof Job) => {
    setSort((prev) => ({
      field,
      direction:
        prev.field === field && prev.direction === "asc" ? "desc" : "asc",
    }));
  };

  // const todayJobs = filteredJobs.filter(
  //   (job) => isToday(job.incomingDate) || isToday(job.deliveryDate)
  // );

  // const overdueJobs = filteredJobs.filter(
  //   (job) => isOverdue(job.deliveryDate) && job.statusDropdown !== "Completed"
  // );

  // const dueSoonJobs = filteredJobs.filter(
  //   (job) => isDueSoon(job.deliveryDate) && job.statusDropdown !== "Completed"
  // );

  return (
    <>
      {/* Filters */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Filter className="h-5 w-5" />
            Filters
          </CardTitle>
          <CardDescription>Filter and search jobs</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="relative">
              <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search jobs..."
                value={filters.search}
                onChange={(e) => handleFilterChange("search", e.target.value)}
                className="pl-10"
              />
            </div>

            <div>
              <CustomCombobox<Client>
                options={clients?.data.map((client) => ({
                  value: client.id,
                  label: client.sgCode,
                }))}
                defaultValue={filters.clientId || ""}
                onChange={(value) => handleFilterChange("clientId", value)}
                placeholder="Filter by client"
              />
            </div>

            {/* <Select
              value={filters.statusDropdown}
              onValueChange={(value) =>
                handleFilterChange("statusDropdown", value)
              }
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Filter by status" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Statuses</SelectItem>
                {statusOptions.map((status) => (
                  <SelectItem key={status} value={status}>
                    {status}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select> */}

            {/* <Select
              value={filters.jobType}
              onValueChange={(value) => handleFilterChange("jobType", value)}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Filter by job type" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Job Types</SelectItem>
                {jobTypes.map((jobType) => (
                  <SelectItem key={jobType} value={jobType}>
                    {jobType}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select
              value={filters.vendor}
              onValueChange={(value) => handleFilterChange("vendor", value)}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Filter by vendor" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Vendors</SelectItem>
                {vendors.map((vendor) => (
                  <SelectItem key={vendor} value={vendor}>
                    {vendor}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select
              value={filters.proDone}
              onValueChange={(value) => handleFilterChange("proDone", value)}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="Pro Done" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All</SelectItem>
                <SelectItem value="true">Pro Done</SelectItem>
                <SelectItem value="false">Pro Pending</SelectItem>
              </SelectContent>
            </Select>

            <Select
              value={filters.qcDone}
              onValueChange={(value) => handleFilterChange("qcDone", value)}
            >
              <SelectTrigger className="w-full">
                <SelectValue placeholder="QC Done" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All</SelectItem>
                <SelectItem value="true">QC Done</SelectItem>
                <SelectItem value="false">QC Pending</SelectItem>
              </SelectContent>
            </Select> */}
          </div>
        </CardContent>
      </Card>

      {/* Job Table */}
      <JobTable
        jobs={filteredJobs}
        sort={sort}
        onSort={handleSort}
        onEdit={handleEditJob}
        onDelete={handleDeleteJob}
        onViewDetails={handleViewDetails}
        currentPage={filters.pageNumber}
        totalPages={
          Math.ceil((pagedJobs?.totalRecords ?? 0) / filters.pageSize) || 0
        }
        onPageChange={setCurrentPage}
        totalItems={pagedJobs?.totalRecords || 0}
        itemsPerPage={filters.pageSize}
      />
    </>
  );
};

export default JobTableContainer;
