"use client";

import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { ScrollArea } from "@/components/ui/scroll-area";
import { SelectItem } from "@/components/ui/select";
import { useSkillMutation } from "@/hooks/useSkillMutation";
import useSkillQuery from "@/hooks/useSkillQuery";
import { getClients } from "@/lib/api/clientApi";
import {
  createJob,
  getJobDeliveryTypes,
  getJobStatuses,
  getJobTypes,
  getJobWorkFlowStatuses,
  updateJob,
} from "@/lib/api/jobApi";
import { formatCurrency } from "@/lib/currencyHelper";
import { toBangladeshTime } from "@/lib/timeHelper";
import {
  defaultJobValues,
  JobFormSchema,
  jobSchema,
} from "@/lib/validation/job";
import { zodResolver } from "@hookform/resolvers/zod";
import { Calculator, Folder } from "lucide-react";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { toast } from "sonner";
import type { Job } from "../../types/job";
import CustomRHFCheckbox from "../ui/CustomRHFCheckbox";
import CustomRHFCombobox from "../ui/CustomRHFCombobox";
import CustomRHFDateInput from "../ui/CustomRHFDateInput";
import CustomRHFInput from "../ui/CustomRHFInput";
import CustomRHFSelect from "../ui/CustomRHFSelect";
import CustomRHFTimePicker from "../ui/CustomRHFTimePicker";

interface CreateJobDialogProps {
  isOpen: boolean;
  onClose: () => void;
  job?: Job | null;
}

const CreateJobDialog = ({ isOpen, onClose, job }: CreateJobDialogProps) => {
  // #region Form
  const {
    register,
    handleSubmit,
    reset,
    setValue,
    watch,
    formState: { errors, isSubmitting },
  } = useForm<JobFormSchema>({
    resolver: zodResolver(jobSchema),
  });

  // #region API Calls
  const { data: clients } = useSkillQuery({
    queryKey: ["clients"],
    queryFn: () => getClients(),
  });

  const { data: jobTypes } = useSkillQuery({
    queryKey: ["jobTypes"],
    queryFn: () => getJobTypes(),
  });

  const { data: jobStatuses } = useSkillQuery({
    queryKey: ["jobStatuses"],
    queryFn: () => getJobStatuses(),
  });

  const { data: jobWorkflowStatuses } = useSkillQuery({
    queryKey: ["jobWorkflowStatuses"],
    queryFn: () => getJobWorkFlowStatuses(),
  });

  const { data: jobDeliveryTypes } = useSkillQuery({
    queryKey: ["jobDeliveryTypes"],
    queryFn: () => getJobDeliveryTypes(),
  });

  const { mutateAsync: createJobFunc } = useSkillMutation<JobFormSchema, Job>({
    queryKey: ["jobs"],
    mutationFn: (job) => createJob(job),
    onClose,
  });

  const { mutateAsync: updateJobFunc } = useSkillMutation<
    { job: JobFormSchema; id: string },
    Job
  >({
    queryKey: ["jobs"],
    mutationFn: ({ job, id }) => updateJob({ job, id }),
    onClose,
  });

  // #region State
  const [incomingDate, setIncomingDate] = useState<Date | undefined>(undefined);
  const [deliveryDate, setDeliveryDate] = useState<Date | undefined>(undefined);

  const [mainFolderPath, unitPrice, inputCount, lockTime, clientId] = watch([
    "mainFolderPath",
    "unitPrice",
    "inputCount",
    "lockTime",
    "clientId",
  ]);

  useEffect(() => {
    if (job) {
      reset(job);
      setIncomingDate(toBangladeshTime(job.incomingDate));
      setDeliveryDate(toBangladeshTime(job.deliveryDate));
      setValue(
        "incomingDate",
        toBangladeshTime(job.incomingDate) || new Date()
      );
      setValue(
        "deliveryDate",
        toBangladeshTime(job.deliveryDate) || new Date()
      );
      setValue(
        "deliveryTime",
        toBangladeshTime(job.deliveryTime) || new Date()
      );
    } else {
      reset(defaultJobValues);
    }
  }, [job, isOpen, reset, setValue]);

  // Calculate total time whenever relevant fields change

  // #region Submit
  const onSubmit = async (data: JobFormSchema) => {
    const toastId = toast.loading("Saving...");

    try {
      if (job) {
        const res = await updateJobFunc({ job: data, id: job.id });
        if (res.success)
          toast.success("Job updated successfully!", { id: toastId });
      } else {
        const res = await createJobFunc(data);
        if (res.success)
          toast.success("Job created successfully!", { id: toastId });
      }
    } catch (error) {
      console.error(error);
    }
    setTimeout(() => {
      toast.dismiss(toastId);
    }, 3000);
  };

  const handleClose = () => {
    onClose();
  };

  const handleDateChange = ({ field, date }: { field: string; date: Date }) => {
    if (field === "incomingDate") setIncomingDate(date);
    else setDeliveryDate(date);
  };

  let folderName = "";
  if (mainFolderPath) {
    const parts = mainFolderPath.split(/[/\\]/);
    folderName = parts[parts.length - 1];
  }

  const clientCurrency = clients?.data.find((c) => c.id === clientId)?.currency;

  return (
    <Dialog open={isOpen} onOpenChange={handleClose}>
      <DialogContent className="min-w-[80vw] lg:min-w-5xl">
        <DialogHeader>
          <DialogTitle>{job ? "Edit Job" : "Create New Job"}</DialogTitle>
          <DialogDescription>
            {job
              ? "Update job information"
              : "Enter job details to create a new job."}
          </DialogDescription>
        </DialogHeader>

        <ScrollArea className="max-h-[70vh] pr-6">
          <form className="space-y-6">
            {/* Client Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Client Information</CardTitle>
                <CardDescription>
                  Enter client code to identify the client
                </CardDescription>
              </CardHeader>
              <CardContent>
                <CustomRHFCombobox
                  id="clientId"
                  label="Select Client"
                  options={clients?.data.map((c) => ({
                    value: c.id,
                    label: c.sgCode,
                  }))}
                  setValue={setValue}
                  errors={errors}
                  defaultValue={job?.clientId}
                />
              </CardContent>
            </Card>

            {/* File Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg flex items-center gap-2">
                  <Folder className="h-5 w-5" />
                  Folder Information
                </CardTitle>
                <CardDescription>
                  Configure file paths and folder structure
                </CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <CustomRHFInput
                  register={register}
                  errors={errors}
                  id="mainFolderPath"
                  label="Folder Path"
                  placeholder="\DOTNET\Infrastructure or /clients/projects/folder"
                  helperText="Supports both Windows (\) and Unix (/) path separators"
                />

                <div className="space-y-2">
                  <Label htmlFor="folderName">Folder Name / Job Code</Label>
                  <Input
                    id="folderName"
                    readOnly
                    value={folderName}
                    placeholder="Last directory from file path"
                    className="bg-muted"
                  />
                  <p className="text-xs text-muted-foreground">
                    Automatically extracted from the last directory in file path
                  </p>
                </div>
              </CardContent>
            </Card>

            {/* Job Details */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Job Details</CardTitle>
                <CardDescription>
                  Configure job type, services, and file counts
                </CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                <CustomRHFSelect
                  setValue={setValue}
                  errors={errors}
                  id="jobTypeId"
                  label="Job Type"
                  defaultValue={job?.jobTypeId}
                >
                  {jobTypes?.data.map((type) => (
                    <SelectItem key={type.id} value={type.id}>
                      {type.name}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <CustomRHFInput
                  register={register}
                  errors={errors}
                  id="vendor"
                  label="Vendor"
                />

                <CustomRHFInput
                  register={register}
                  errors={errors}
                  id="inputCount"
                  label="Input Files"
                  valueAsNumber
                />

                {/* Only show output field when editing */}

                <CustomRHFInput
                  register={register}
                  errors={errors}
                  id="unitPrice"
                  label="Unit Price"
                  valueAsNumber
                />
              </CardContent>
            </Card>

            {/* Time Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Time Information</CardTitle>
                <CardDescription>
                  Configure time-related fields (in numerical format)
                </CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <CustomRHFInput
                  register={register}
                  errors={errors}
                  id="lockTime"
                  label="Lock Time"
                  valueAsNumber
                />

                <CustomRHFInput
                  register={register}
                  errors={errors}
                  id="modifiedTime"
                  label="Modified Time"
                  valueAsNumber
                />
              </CardContent>
            </Card>

            {/* Schedule Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Schedule Information</CardTitle>
                <CardDescription>
                  Set incoming and delivery dates
                </CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                <CustomRHFDateInput
                  errors={errors}
                  setValue={setValue}
                  id="incomingDate"
                  label="Incoming Date"
                  dateValue={incomingDate}
                  handleDateChange={handleDateChange}
                />

                <CustomRHFDateInput
                  errors={errors}
                  setValue={setValue}
                  id="deliveryDate"
                  label="Delivery Date"
                  dateValue={deliveryDate}
                  handleDateChange={handleDateChange}
                />

                <CustomRHFTimePicker
                  setValue={setValue}
                  errors={errors}
                  id="deliveryTime"
                  label="Delivery Time"
                  type="time"
                  step="1"
                />

                <CustomRHFSelect
                  setValue={setValue}
                  errors={errors}
                  id="deliveryType"
                  label="Delivery Type"
                  defaultValue={job?.deliveryType?.toString()}
                  valueAsNumber
                >
                  {jobDeliveryTypes?.data.map((status) => (
                    <SelectItem
                      key={status.value}
                      value={status.value.toString()}
                    >
                      {status.name}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>
              </CardContent>
            </Card>

            {/* Status Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Status Information</CardTitle>
                <CardDescription>
                  Configure job status and progress
                </CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <CustomRHFSelect
                  setValue={setValue}
                  errors={errors}
                  id="status"
                  label="Status"
                  defaultValue={job?.status?.toString()}
                  valueAsNumber
                >
                  {jobStatuses?.data.map((status) => (
                    <SelectItem
                      key={status.value}
                      value={status.value.toString()}
                    >
                      {status.name}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <CustomRHFSelect
                  setValue={setValue}
                  errors={errors}
                  id="workflowStatus"
                  label="Status (Workflow)"
                  defaultValue={job?.workflowStatus?.toString()}
                  valueAsNumber
                >
                  {jobWorkflowStatuses?.data.map((status) => (
                    <SelectItem
                      key={status.value}
                      value={status.value.toString()}
                    >
                      {status.name}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <div className="flex items-center space-x-2">
                  <CustomRHFCheckbox
                    setValue={setValue}
                    errors={errors}
                    label="Production Done"
                    id="proDone"
                    defaultChecked={job?.proDone}
                  />
                </div>

                <div className="flex items-center space-x-2">
                  <CustomRHFCheckbox
                    setValue={setValue}
                    errors={errors}
                    label="QC Done"
                    id="qcDone"
                    defaultChecked={job?.qcDone}
                  />
                </div>
              </CardContent>
            </Card>

            {/* Calculated Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg flex items-center gap-2">
                  <Calculator className="h-5 w-5" />
                  Calculated Information
                </CardTitle>
                <CardDescription>
                  Auto-calculated values based on input data
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label>Total Time (Minutes)</Label>
                    <div className="p-3 bg-muted rounded-md">
                      <span className="font-mono text-lg">
                        {(Number(inputCount) || 0) * (Number(lockTime) || 0)}
                      </span>
                      <span className="text-sm text-muted-foreground ml-2">
                        minutes
                      </span>
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label>Estimated Total Cost</Label>
                    <div className="p-3 bg-muted rounded-md">
                      <span className="font-mono text-lg">
                        {(Number(unitPrice) || 0) * (Number(inputCount) || 0)}{" "}
                        {formatCurrency(clientCurrency)}
                      </span>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </form>
        </ScrollArea>

        <DialogFooter>
          <Button type="button" variant="outline" onClick={handleClose}>
            Cancel
          </Button>
          <Button
            type="submit"
            onClick={handleSubmit(onSubmit)}
            disabled={isSubmitting}
          >
            {job ? "Update Job" : "Create Job"}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
};

export default CreateJobDialog;
