"use client";

import type React from "react";

import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import useSkillQuery from "@/hooks/useSkillQuery";
import { getJobStatuses } from "@/lib/api/jobApi";
import getStatusName from "@/lib/enumHelper";
import { formatDate } from "@/lib/utils";
import {
  Calendar,
  CheckCircle,
  ChevronDown,
  ChevronLeft,
  ChevronRight,
  ChevronsLeft,
  ChevronsRight,
  ChevronUp,
  Clock,
  Edit,
  FileInput,
  FileOutput,
  MoreHorizontal,
  Trash2,
  XCircle,
} from "lucide-react";
import type { Job, JobSort } from "../../types/job";

interface JobTableProps {
  jobs: Job[];
  sort: JobSort;
  onSort: (field: keyof Job) => void;
  onEdit: (job: Job) => void;
  onDelete: (jobId: string) => void;
  onViewDetails: (job: Job) => void;
  currentPage: number;
  totalPages: number;
  onPageChange: (page: number) => void;
  totalItems: number;
  itemsPerPage: number;
}

