//       return "outline";
//     default:
//       return "outline";
//   }
// };

// const getClientCode = (clientId: string) => {
//   const client = mockClients.find((c) => c.id === clientId);
//   return client ? client.sg : "N/A";
// };

// const getClientCurrency = (clientId: string) => {
//   const client = mockClients.find((c) => c.id === clientId);
//   return client ? client.currency : "USD";
// };

// const formatServices = (services: string[] | undefined | null) => {
//   if (!services || !Array.isArray(services) || services.length === 0)
//     return "-";
//   if (services.length === 1) return services[0];
//   return `${services[0]} +${services.length - 1} more`;
// };

// const getServicesTitle = (services: string[] | undefined | null) => {
//   if (!services || !Array.isArray(services) || services.length === 0)
//     return "No services";
//   return services.join(", ");
// };

export default function JobTable({
  jobs,
  sort,
  onSort,
  onEdit,
  onDelete,
  onViewDetails,
  currentPage,
  totalPages,
  onPageChange,
  totalItems,
  itemsPerPage,
}: JobTableProps) {
  // #region API
  const { data: jobStatuses } = useSkillQuery({
    queryKey: ["jobStatuses"],
    queryFn: () => getJobStatuses(),
  });

  const SortableHeader = ({
    field,
    children,
  }: {
    field: keyof Job;
    children: React.ReactNode;
  }) => (
    <TableHead>
      <Button
        variant="ghost"
        size="sm"
        className="h-auto p-0 font-semibold hover:bg-transparent"
        onClick={() => onSort(field)}
      >
        {children}
        {sort.field === field &&
          (sort.direction === "asc" ? (
            <ChevronUp className="ml-1 h-4 w-4" />
          ) : (
            <ChevronDown className="ml-1 h-4 w-4" />
          ))}
      </Button>
    </TableHead>
  );

  const startItem = (currentPage - 1) * itemsPerPage + 1;
  const endItem = Math.min(currentPage * itemsPerPage, totalItems);

  const handleRowClick = (job: Job, event: React.MouseEvent) => {
    // Prevent row click when clicking on action buttons or dropdown menu items
    if (
      (event.target as HTMLElement).closest("[data-action-button]") ||
      (event.target as HTMLElement).closest("[role='menuitem']") ||
      (event.target as HTMLElement).closest(
        "[data-radix-popper-content-wrapper]"
      )
    ) {
      return;
    }
    onViewDetails(job);
  };

  // const handleViewDetailsClick = (job: Job, event: React.MouseEvent) => {
  //   event.stopPropagation();
  //   onViewDetails(job);
  // };

  const handleEditJobClick = (job: Job, event: React.MouseEvent) => {
    event.stopPropagation();
    onEdit(job);
  };

  const handleDeleteJobClick = (jobId: string, event: React.MouseEvent) => {
    event.stopPropagation();
    onDelete(jobId);
  };

  return (
    <div className="space-y-4">
      <Card>
        <CardContent className="p-0">
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <SortableHeader field="clientId">Client Code</SortableHeader>
                  <SortableHeader field="jobCode">Job Code</SortableHeader>
                  <TableHead>Input/Output</TableHead>
                  <SortableHeader field="incomingDate">
                    Incoming Date
                  </SortableHeader>
                  <SortableHeader field="deliveryDate">Delivery</SortableHeader>
                  <SortableHeader field="jobType">Job Type</SortableHeader>
                  <SortableHeader field="status">Status</SortableHeader>
                  <SortableHeader field="unitPrice">Unit Price</SortableHeader>
                  <SortableHeader field="actualTime">
                    Total Time (min)
                  </SortableHeader>
                  <TableHead>Progress</TableHead>
                  <TableHead className="w-[50px]">Actions</TableHead>
                  <SortableHeader field="vendor">Vendor</SortableHeader>
                </TableRow>
              </TableHeader>
              <TableBody>
                {jobs.length === 0 ? (
                  <TableRow>
                    <TableCell
                      colSpan={14}
                      className="text-center py-8 text-muted-foreground"
                    >
                      No jobs found
                    </TableCell>
                  </TableRow>
                ) : (
                  jobs.map((job) => (
                    <TableRow
                      key={job.id}
                      className="cursor-pointer hover:bg-muted/50 transition-colors"
                      onClick={(e) => handleRowClick(job, e)}
                    >
                      <TableCell>
                        <Badge variant="outline" className="text-xs w-fit">
                          {job.client?.name}
                        </Badge>
                      </TableCell>
                      <TableCell className="font-medium max-w-[200px]">
                        <div
                          className="truncate"
                          title={job.mainFolderPath || "No folder name"}
                        >
                          {job.jobCode || "-"}
                        </div>
                      </TableCell>
                      <TableCell>
                        <div className="flex flex-col text-sm">
                          <div className="flex items-center gap-1">
                            <FileInput className="h-3 w-3 text-blue-500" />
                            <span>In: {job.inputCount || 0}</span>
                          </div>
                          <div className="flex items-center gap-1">
                            <FileOutput className="h-3 w-3 text-green-500" />
                            <span>Out: {job.outputCount || 0}</span>
                          </div>
                        </div>
                      </TableCell>
                      <TableCell>
                        <div className="flex flex-col text-sm">
                          <div className="flex items-center gap-1">
                            <Calendar className="h-3 w-3 text-blue-500" />
                            <span>{formatDate(job.incomingDate)}</span>
                          </div>
                        </div>
                      </TableCell>
                      <TableCell>
                        <div className="flex flex-col text-sm">
                          {/* <span
                            className={`${
                              isOverdue(job.deliveryDate) &&
                              job.statusDropdown !== "Completed"
                                ? "text-red-600 font-medium"
                                : ""
                            }`}
                          >
                            {getRelativeDateLabel(job.delivery)}
                          </span> */}
                          <div className="flex items-center gap-1 text-xs text-muted-foreground">
                            <Clock className="h-3 w-3" />
                            {formatDate(job.deliveryDate) || "No time"}
                            {/* {isToday(job.deliveryDate) && (
                              <Badge variant="default" className="text-xs ml-1">
                                Today
                              </Badge>
                            )} */}
                            {/* {isOverdue(job.deliveryDate) &&
                              job.ty !== "Completed" && (
                                <Badge
                                  variant="destructive"
                                  className="text-xs ml-1"
                                >
                                  Overdue
                                </Badge>
                              )}
                            {isDueSoon(job.deliveryDate) &&
                              job.statusDropdown !== "Completed" && (
                                <Badge
                                  variant="outline"
                                  className="text-xs ml-1"
                                >
                                  Due Soon
                                </Badge>
                              )} */}
                          </div>
                        </div>
                      </TableCell>
                      <TableCell className="max-w-[150px]">
                        <div
                          className="truncate"
                          title={job.jobType?.name || "No job type"}
                        >
                          {job.jobType?.name || "-"}
                        </div>
                      </TableCell>
                      <TableCell>
                        <Badge
                          // variant={getStatusBadgeVariant(job.status)}
                          className="text-xs"
                        >
                          {getStatusName(jobStatuses?.data || [], job.status)}
                        </Badge>
                      </TableCell>

                      <TableCell>
                        {job.unitPrice}
                        {/* {job.unitPrice
                          ? formatCurrency(
                              job.unitPrice,
                              getClientCurrency(job.clientId)
                            )
                          : "-"} */}
                      </TableCell>
                      <TableCell className="font-mono text-sm">
                        {/* {job.totalTime || 0} */}
                      </TableCell>
                      <TableCell>
                        <div className="flex items-center gap-2">
                          <div className="flex items-center gap-1">
                            {job.proDone ? (
                              <CheckCircle className="h-4 w-4 text-green-500" />
                            ) : (
                              <XCircle className="h-4 w-4 text-gray-400" />
                            )}
                            <span className="text-xs">Pro</span>
                          </div>
                          <div className="flex items-center gap-1">
                            {job.qcDone ? (
                              <CheckCircle className="h-4 w-4 text-green-500" />
                            ) : (
                              <XCircle className="h-4 w-4 text-gray-400" />
                            )}
                            <span className="text-xs">QC</span>
                          </div>
                        </div>
                      </TableCell>
                      <TableCell>
                        <div data-action-button="true">
                          <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                              <Button
                                variant="ghost"
                                size="sm"
                                className="h-8 w-8 p-0"
                              >
                                <MoreHorizontal className="h-4 w-4" />
                                <span className="sr-only">Open menu</span>
                              </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end">
                              <DropdownMenuItem
                                onClick={(e) => handleEditJobClick(job, e)}
                              >
                                <Edit className="mr-2 h-4 w-4" />
                                Edit
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                onClick={(e) => handleDeleteJobClick(job.id, e)}
                                className="text-destructive"
                              >
                                <Trash2 className="mr-2 h-4 w-4" />
                                Delete
                              </DropdownMenuItem>
                            </DropdownMenuContent>
                          </DropdownMenu>
                        </div>
                      </TableCell>
                      <TableCell className="max-w-[120px]">
                        <div
                          className="truncate"
                          title={job.vendor || "No vendor"}
                        >
                          {job.vendor || "-"}
                        </div>
                      </TableCell>
                    </TableRow>
                  ))
                )}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="flex items-center justify-between">
          <div className="text-sm text-muted-foreground">
            Showing {startItem} to {endItem} of {totalItems} jobs
          </div>
          <div className="flex items-center space-x-2">
            <Button
              variant="outline"
              size="sm"
              onClick={() => onPageChange(1)}
              disabled={currentPage === 1}
            >
              <ChevronsLeft className="h-4 w-4" />
              <span className="sr-only">First page</span>
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => onPageChange(currentPage - 1)}
              disabled={currentPage === 1}
            >
              <ChevronLeft className="h-4 w-4" />
              <span className="sr-only">Previous page</span>
            </Button>
            <div className="flex items-center space-x-1">
              {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                const pageNumber =
                  Math.max(1, Math.min(totalPages - 4, currentPage - 2)) + i;
                return (
                  <Button
                    key={pageNumber}
                    variant={currentPage === pageNumber ? "default" : "outline"}
                    size="sm"
                    onClick={() => onPageChange(pageNumber)}
                    className="w-8 h-8 p-0"
                  >
                    {pageNumber}
                  </Button>
                );
              })}
            </div>
            <Button
              variant="outline"
              size="sm"
              onClick={() => onPageChange(currentPage + 1)}
              disabled={currentPage === totalPages}
            >
              <ChevronRight className="h-4 w-4" />
              <span className="sr-only">Next page</span>
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => onPageChange(totalPages)}
              disabled={currentPage === totalPages}
            >
              <ChevronsRight className="h-4 w-4" />
              <span className="sr-only">Last page</span>
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}
