"use client";

import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import useSkillQuery from "@/hooks/useSkillQuery";
import { getPagedJobs } from "@/lib/api/jobApi";
import { formatDateForDisplay, getCurrentDate } from "@/lib/date-utils";
import { defaultPageSize } from "@/types/base";
import { JobFilters, type Job } from "@/types/job";
import { Download, Plus } from "lucide-react";
import { useState } from "react";
import ErrorPage from "../error-page";
import CreateJobDialog from "./create-job-dialog";
import JobDetailsModal from "./job-details-modal";
import JobTableContainer from "./job-table-container";

export const serviceOptions = [
  "CP", // Clipping Path
  "Ret", // Retouching
  "Liq", // Liquify
  "Sha", // Shadow
  "CR", // Color Correction/Color Retouching
  "BG", // Background Removal
  "Res", // Resize
  "Enh", // Enhancement
  "Adj", // Adjustment
  "Comp", // Composite
  "Mask", // Masking
  "Vec", // Vector
  "Logo", // Logo Design
  "Banner", // Banner Design
  "Layout", // Layout Design
  "model creation", // From your data
  "Trashion", // From your data
  "subtitle", // From your data
  "image generate", // From your data
];

// Status Options - From your spreadsheet Status column
export const statusOptions = [
  "Downloading",
  "Need to up",
  "Uploading",
  "Cancel",
  "Delivered",
  "Postponed",
];

export default function JobManagement() {
  // Filter options state
  const [filters, setFilters] = useState<JobFilters>({
    search: "",
    clientId: "",
    pageNumber: 1,
    pageSize: defaultPageSize,
  });

  const { data: jobs, isFetching: isJobsLoading } = useSkillQuery({
    queryKey: ["jobs", filters],
    queryFn: () => getPagedJobs(filters),
  });

  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [isDetailsModalOpen, setIsDetailsModalOpen] = useState(false);
  const [editingJob, setEditingJob] = useState<Job | null>(null);
  const [selectedJob, setSelectedJob] = useState<Job | null>(null);

  // Handle filter changes

  // Handle job operations
  const handleCreateJob = () => {
    setEditingJob(null);
    setIsDialogOpen(true);
  };

  const handleEditJob = (job: Job) => {
    setEditingJob(job);
    setIsDialogOpen(true);
  };

  const handleViewDetails = (job: Job) => {
    setSelectedJob(job);
    setIsDetailsModalOpen(true);
  };

  const handleDeleteJob = (jobId: string) => {
    console.log("Delete job with ID:", jobId);
  };

  if (!isJobsLoading && !jobs?.success)
    <ErrorPage
      message={jobs?.message ?? "Something went wrong on our end."}
      status={500}
    />;

  return (
    <div className="space-y-6">
      {/* Header Actions */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div className="flex items-center gap-2">
          <Badge variant="secondary" className="text-sm">
            {jobs?.data?.items?.length || 0} jobs
          </Badge>
          <Badge variant="outline" className="text-xs">
            As of {formatDateForDisplay(getCurrentDate())}
          </Badge>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm">
            <Download className="h-4 w-4 mr-2" />
            Export
          </Button>
          <Button onClick={handleCreateJob} size="sm">
            <Plus className="h-4 w-4 mr-2" />
            Create Job
          </Button>
        </div>
      </div>

      <JobTableContainer
        pagedJobs={jobs?.data}
        filters={filters}
        setFilters={setFilters}
        handleViewDetails={handleViewDetails}
        handleEditJob={handleEditJob}
        handleDeleteJob={handleDeleteJob}
      />

      {/* Create Job Dialog */}
      <CreateJobDialog
        isOpen={isDialogOpen}
        onClose={() => setIsDialogOpen(false)}
        job={editingJob}
      />

      {/* Job Details Modal */}
      <JobDetailsModal
        isOpen={isDetailsModalOpen}
        onClose={() => setIsDetailsModalOpen(false)}
        job={selectedJob}
        onEdit={handleEditJob}
      />
    </div>
  );
}
