import { FieldErrors, FieldValues, Path, SetFieldValue } from "react-hook-form";
import { Checkbox } from "../checkbox";
import { Label } from "../label";

interface CustomRHFCheckboxProps<T extends FieldValues> {
  setValue: SetFieldValue<T>;
  label: string;
  id: Path<T>;
  placeholder?: string;
  errors: FieldErrors<T>;
  helperText?: string;
  defaultChecked?: boolean;
}

const CustomRHFCheckbox = <T extends FieldValues>({
  errors,
  setValue,
  label,
  id,
  helperText,
  defaultChecked,
}: CustomRHFCheckboxProps<T>) => {
  const error = errors[id];

  return (
    <div className="flex gap-2 items-center">
      <Checkbox
        id={id}
        className={error ? "border-destructive" : ""}
        defaultChecked={defaultChecked}
        onCheckedChange={(checked) => {
          setValue(id, Boolean(checked), { shouldValidate: true });
        }}
      />
      <Label htmlFor={id}>{label}</Label>

      {error && (
        <p className="text-sm text-destructive">
          {(error as { message?: string }).message}
        </p>
      )}

      {helperText && (
        <p className="text-xs text-muted-foreground">{helperText}</p>
      )}
    </div>
  );
};

export default CustomRHFCheckbox;
