import { z } from "zod";

/**
 * Job schema (all fields required, using .min for stronger validation)
 */
export const jobSchema = z.object({
  mainFolderPath: z.string().min(1, "Main FolderPath is required"),
  inputCount: z.number().int().min(1, "Input Count must be at least 1"),
  vendor: z.string().min(1, "Vendor is required"),
  unitPrice: z.number().min(1, "Unit Price must be at least 1"),

  // Time Information (timestamps in ms/seconds, must be >= 1)
  lockTime: z.number().min(1, "Lock Time is required"),
  modifiedTime: z.number().min(1, "Modified Time is required"),

  // Schedule Information
  incomingDate: z.date({ error: "Incoming Date is required" }),
  deliveryDate: z.date({ error: "Delivery Date is required" }),
  deliveryTime: z.date({ error: "Delivery Time is required" }),
  deliveryType: z.number().min(0, "Delivery Type is required"),

  // Status Information
  status: z.number().min(0, "Status is required"),
  workflowStatus: z.number().min(0, "Workflow Status is required"),
  proDone: z.boolean(),
  qcDone: z.boolean(),

  // Foreign Ids
  clientId: z.string().min(1, "Client is required"),
  jobTypeId: z.string().min(1, "Job Type is required"),
});

/** TS type extracted from schema */
export type JobFormSchema = z.infer<typeof jobSchema>;

export const defaultJobValues: JobFormSchema = {
  mainFolderPath: "",
  inputCount: 0,
  vendor: "",
  unitPrice: 0,
  lockTime: 0,
  modifiedTime: 0,
  incomingDate: undefined as unknown as Date,
  deliveryDate: undefined as unknown as Date,
  deliveryTime: undefined as unknown as Date,
  deliveryType: -1,
  status: -1,
  workflowStatus: -1,
  proDone: false,
  qcDone: false,
  clientId: "",
  jobTypeId: "",
};
