import { FieldErrors, FieldValues, Path, SetFieldValue } from "react-hook-form";
import { Input } from "../input";
import { Label } from "../label";

interface CustomRHFTimePickerProps<T extends FieldValues>
  extends Omit<React.InputHTMLAttributes<HTMLInputElement>, "onChange"> {
  setValue: SetFieldValue<T>;
  label: string;
  id: Path<T>;
  errors: FieldErrors<T>;
  helperText?: string;
}

const CustomRHFTimePicker = <T extends FieldValues>({
  setValue,
  label,
  id,
  errors,
  helperText,
  ...props
}: CustomRHFTimePickerProps<T>) => {
  const error = errors[id];

  return (
    <div className="space-y-2">
      <Label htmlFor={id}>{label}</Label>

      <Input
        id={id}
        type="time"
        className={error ? "border-destructive" : ""}
        onChange={(e) => {
          const [hours, minutes] = e.target.value.split(":").map(Number);

          const date = new Date();
          date.setHours(hours || 0);
          date.setMinutes(minutes || 0);
          date.setSeconds(0);
          date.setMilliseconds(0);

          setValue(id, date, { shouldValidate: true });
        }}
        {...props}
      />

      {error && (
        <p className="text-sm text-destructive">
          {(error as { message?: string }).message}
        </p>
      )}

      {helperText && (
        <p className="text-xs text-muted-foreground">{helperText}</p>
      )}
    </div>
  );
};

export default CustomRHFTimePicker;
