"use client";

import { Check, ChevronsUpDown } from "lucide-react";

import { Button } from "@/components/ui/button";
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
  CommandList,
} from "@/components/ui/command";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { cn } from "@/lib/utils";
import { useState } from "react";
import { FieldErrors, FieldValues, Path, SetFieldValue } from "react-hook-form";
import { Label } from "../label";

interface CustomRHFComboboxProps<T extends FieldValues> {
  label: string;
  id: Path<T>;
  placeholder?: string;
  setValue: SetFieldValue<T>;
  errors: FieldErrors<T>;
  defaultValue?: string;
  options?: {
    value: string;
    label: string;
  }[];
}

const CustomRHFCombobox = <T extends FieldValues>({
  label,
  id,
  placeholder,
  setValue,
  errors,
  defaultValue,
  options,
}: CustomRHFComboboxProps<T>) => {
  const [open, setOpen] = useState<boolean>(false);
  const [selectedValue, setSelectedValue] = useState<string | undefined>(
    defaultValue
  );

  const error = errors[id];

  return (
    <Popover open={open} onOpenChange={setOpen}>
      <Label>{label}</Label>
      <PopoverTrigger asChild>
        <Button
          variant="outline"
          role="combobox"
          aria-expanded={open}
          className={cn(
            "w-full justify-between text-left font-normal my-2",
            error && "border-destructive"
          )}
        >
          {selectedValue
            ? options?.find((option) => option.value === selectedValue)?.label
            : placeholder
            ? placeholder
            : `Select ${label}`}
          <ChevronsUpDown className="opacity-50" />
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-fit p-0">
        <Command>
          <CommandInput placeholder="Search option..." className="h-9" />
          <CommandList>
            <CommandEmpty>No option found.</CommandEmpty>
            <CommandGroup>
              {options?.map((option) => (
                <CommandItem
                  key={option.value}
                  value={option.value}
                  onSelect={(currentValue) => {
                    setSelectedValue(
                      currentValue === selectedValue ? "" : currentValue
                    );
                    setValue(id, currentValue);
                    setOpen(false);
                  }}
                >
                  {option.label}
                  <Check
                    className={cn(
                      "ml-auto",
                      selectedValue === option.value
                        ? "opacity-100"
                        : "opacity-0"
                    )}
                  />
                </CommandItem>
              ))}
            </CommandGroup>
          </CommandList>
        </Command>
      </PopoverContent>
      {error && (
        <p className="text-sm text-destructive">
          {(error as { message?: string }).message}
        </p>
      )}
    </Popover>
  );
};

export default CustomRHFCombobox;
